
import Cell from 'vux/src/components/cell/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Group from 'vux/src/components/group/index.vue'

import {withdrawlog} from '../config'
export default {
  head: {
    title: {
      inner: '提现记录'
    }
  },
  data () {
    return {
      height: '0px',
      list: [],
      form: {
        userId: '',
        limit: 10,
        pageIndex: 0
      }
    }
  },
  components: {
    Cell,
    dateFormat,
    Group
  },
  created () {
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
    this.getList()
  },
  methods: {
    getList () {
      this.$http({
        method: 'jsonp',
        url: withdrawlog,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form
      })
      .then(res => {
        console.log(res)
        this.list = res.body.data.scoreList
        for (const i in this.list) {
          this.list[i].createTime = dateFormat(this.list[i].createTime)
        }
      })
    },
    handleSave (item) {
      console.log(item)
      this.$localStorage.set('record', JSON.stringify(item))
    }
  }
}



